@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  body {
    @apply m-0 p-0 box-border;
  }
}

@layer components {
  .topPadding {
    @apply pt-[7rem];
  }

  .card {
    @apply mt-[5rem] min-h-[70vh] bg-white;
  }

  .maxWidth {
    @apply lg:max-w-[1040px] lg:mx-auto;
  }

  .loadingBg {
    @apply bg-slate-100 animate-pulse;
  }
}
